/* width */
.scroll-chat::-webkit-scrollbar {
  width: 8px;
}

/* Track */
.scroll-chat::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #fff; 
  border-radius: 10px;
}
 
/* Handle */
.scroll-chat::-webkit-scrollbar-thumb {
  background: #B8C2CE; 
  border-radius: 10px;
}

/* Handle on hover */
.scroll-chat::-webkit-scrollbar-thumb:hover {
  background: #ddd; 
}


/* width */
.scroll-messages::-webkit-scrollbar {
