.scroll-messages::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #fff; 
  border-radius: 10px;
}
 
/* Handle */
.scroll-messages::-webkit-scrollbar-thumb {
  background: #F1F5F9; 
  border-radius: 10px;
}

/* Handle on hover */
.scroll-messages::-webkit-scrollbar-thumb:hover {
  background: #ddd; 
}

.message-write-section .input-group-text{
  background-color: transparent;
}
