/* complete-profile-page */

.personal-details-section .input-group-text{
  height: 45px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 5px 10px 20px #697F961A;
  border: 1px solid #F1F5F9;
  border-radius: 7px;
  border-right: 0px;
}

.personal-details-section .form-control{
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 5px 10px 20px #697F961A;
