  border-radius: 7px;
  border-left: 0px;
}

.speciality-section p {
  background-color: #697F96;
  color: #fff;
  padding: 10px 20px;
  box-shadow: 0px 6px 10px #697F9626;
  border-radius: 5px;
  margin-right: 10px;
}

.speciality-section input[type="checkbox"]:not(:checked), 
.speciality-section  input[type="checkbox"]:checked {
  position: absolute;
  left: -9999%;
}

.speciality-section input[type="checkbox"] + label {
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  color: #fff;
  background-color: #697F96;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 0px 6px 10px #697F9626;
  user-select: none;
  margin-right: 15px;
}

.speciality-section input[type="checkbox"]:checked + label {
  border: 1px solid #B8C2CE;
  color: #B8C2CE;
  background-color: #fff;
}

.complete-profile-wrapper .form-select{
  background:url(../public/chevron-down.svg) ,transparent linear-gradient(180deg, #E3EBF3 0%, #F1F5F9 100%) ;
  border: 0px solid;
  background-repeat: no-repeat;
